<template>
  <div id="logo">
    <img id="img" alt="Vue logo" src="./assets/me.jpg">
    <div style="text-align: center;">
      <button id="clear" @click="clearCache">清除缓存</button>
    </div>
  </div>
  <HelloWorld />
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  methods: {
    clearCache() {
      if (localStorage) {
        localStorage.clear();
      }
      if (sessionStorage) {
        sessionStorage.clear();
      }
      console.log('缓存已清除');
    },
  }
}
</script>

