<template>
  <div id="hello">
    <div v-if="loading" class="progress-bar-container">
      <span style="position: absolute;">图片加载中...</span>
      <div class="progress-bar" :style="{ width: loadProgress + '%' }"></div>
    </div>

    <ul>
      <div class="title"><span>日常常用</span></div>
      <li class="box" v-for="(item, index) in items" :key="index" @click="copyImageOrTextToClipboard(item, $event)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <img v-if="item.img" :src="item.img" @load="handleImageLoad" @error="handleImageError" style="display:none;" />
        <p class="sub" v-if="index == 7">每次复制,都会随机复制微信号</p>
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div class="title"><span>软件使用</span></div>
      <li class="box" v-for="(item, index) in app" :key="index" @click="copyImageOrTextToClipboard(item, $event)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <img v-if="item.img" :src="item.img" @load="handleImageLoad" @error="handleImageError" style="display:none;" />
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div class="title"><span>iOS & iPad</span></div>
      <li class="box" v-for="(item, index) in ios" :key="index" @click="copyImageOrTextToClipboard(item, $event)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <img v-if="item.img" :src="item.img" @load="handleImageLoad" @error="handleImageError" style="display:none;" />
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div class="title"><span>Windows</span></div>
      <li class="box" v-for="(item, index) in windows" :key="index" @click="copyImageOrTextToClipboard(item, $event)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <img v-if="item.img" :src="item.img" @load="handleImageLoad" @error="handleImageError" style="display:none;" />
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div class="title"><span>macOS</span></div>
      <li class="box" v-for="(item, index) in mac" :key="index" @click="copyImageOrTextToClipboard(item, $event)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <img v-if="item.img" :src="item.img" @load="handleImageLoad" @error="handleImageError" style="display:none;" />
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>

    <ul>
      <div class="title"><span>Android</span></div>
      <li class="box" v-for="(item, index) in android" :key="index" @click="copyImageOrTextToClipboard(item, $event)"
        :title="item.content">
        <span>{{ item.title }}</span>
        <img v-if="item.img" :src="item.img" @load="handleImageLoad" @error="handleImageError" style="display:none;" />
      </li>
      <li style="list-style: none;clear: both;"></li>
    </ul>
    <div style="clear: both;"></div>
    <div style="height: 300px"></div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'HelloWorld',
  data() {
    return {
      loading: true,
      loadProgress: 0,
      totalImages: 0, // 要加载的图片总数
      loadedImages: 0 // 已经加载的图片数
    };
  },
  computed: {
    ...mapGetters(['items', 'app', 'ios', 'windows', 'mac', 'android']),
  },
  created() {
    // 初始化 totalImages 为要加载的图片总数
    this.totalImages = this.items.concat(this.app, this.ios, this.windows, this.mac, this.android)
      .filter(item => item.img).length;
  },
  methods: {
    handleImageLoad() {
      // 图片加载成功，更新 loadedImages 和 loadProgress
      this.loadedImages++;
      this.loadProgress = (this.loadedImages / this.totalImages) * 100;
      // 如果所有图片都加载完成，则设置 loading 为 false
      if (this.loadedImages === this.totalImages) {
        this.loading = false;
      }
    },
    handleImageError() {
      // 处理图片加载错误的情况
      console.error('图片加载错误');
      this.loadedImages++;
      this.loadProgress = (this.loadedImages / this.totalImages) * 100;
      if (this.loadedImages === this.totalImages) {
        this.loading = false;
      }
    },
    async copyToClipboard(data) {
      try {
        // 检查浏览器是否支持 Clipboard API
        if (!navigator.clipboard) {
          console.error('当前浏览器不支持Clipboard API');
          return;
        }

        await navigator.clipboard.writeText(data);

        console.log('内容已成功复制到剪贴板');
      } catch (err) {
        console.error('复制到剪贴板失败：', err);
      }
    },

    async copyImageOrTextToClipboard(item, event) {
      if (this.isCopying) {
        return;
      }
      this.isCopying = true;

      this.$store.dispatch('updateContent')

      try {
        if (item.content === '') {
          if (item.img === '') {
            console.error('无可复制内容');
            return;
          }

          console.log()

          const imgBlob = await fetch(item.img).then(response => response.blob());
          const clipboardItemInput = new ClipboardItem({ 'image/png': imgBlob });
          await navigator.clipboard.write([clipboardItemInput]);


          console.log('内容已成功复制到剪贴板');
        } else {
          await this.copyToClipboard(item.content);
        }

        const clickedElement = event.target;
        clickedElement.style.backgroundColor = 'red';
        setTimeout(() => {
          clickedElement.style.backgroundColor = '';
        }, 500);

      } catch (err) {
        console.error('复制到剪贴板失败：', err);
      } finally {
        // 复制操作完成后解除禁用
        setTimeout(() => {
          this.isCopying = false;
        }, 500);
      }
    }
  }
}
</script>

