import { createStore } from "vuex";

let lastRandomIndex = null; // 用来存储上一次的随机索引

function getR() {
    const arr = ["pin222mm", "pin222kk", "pin999kk", 'ins888aa'];
    let currentIndex;

    do {
        currentIndex = Math.floor(Math.random() * arr.length);
    } while (currentIndex === lastRandomIndex); // 确保这次的随机数和上次不一样

    lastRandomIndex = currentIndex; // 更新lastRandomIndex为这次的currentIndex
    return arr[currentIndex];
}

